
.wrapper .jewelry{
color: #eee8e3;
font-family: Sorts Mill Goudy , Sans-serif;
font-size: 240px;
line-height: 345px;
}
.wrapper .selection {
    display: flex;
    justify-content: end;
    color: #eee8e3;
font-family: Sorts Mill Goudy , Sans-serif;
font-size: 240px;
line-height: 345px;
}
.wrapper .text {
    font-family: Sorts Mill Goudy;
    text-align: center;
    font-size: 35px;
    line-height: 1.5em;
}

@media (max-width:500px) {
    .wrapper {
        width: 100%;
       
    }
    .wrapper .jewelry {
        font-size: 100px;
        text-align: center;
        margin-bottom: -20px;
    }
    .wrapper .text {
        font-size: 20px;
        line-height: 1.1em;
    }
    .wrapper .selection {
        display: flex;
        justify-content: center;
        font-size: 90px;
        
    }
    

}
