.categorie .categorie-cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
}
.categorie .categorie-card {
    position: relative;
}
.categorie .categorie-card .content {
    position: absolute;
    width: 100%;
    top: 50%;
    text-align: center;
    font-family: "Sorts Mill Goudy";
    font-size: 40px;
    line-height: 1.5em;
    color: white;
}

@media (max-width:500px) {
    .max-width {
        padding: 0px 0px;
      }
      .categorie{
        position: fixed;
      }
      
    .categorie .categorie-cards{
        display: grid;
        grid-template-rows: repeat(3,1fr);
        grid-template-columns:  1fr;
        gap: 20px;
    }
}