.ocassion{
    margin-top: 150px;
}
.ocassion .ocassion-container{
    display: flex;
    gap: 20px;
    flex-direction: row;
    justify-content: center;
}
.ocassion .ocassion-img img {
    width: 100%;
    height: 90vh;
}
.ocassion .ocassion-img {
    display: flex;
    justify-content: end;
}
.ocassion .ocassion-content .ocasion-title {
    margin-top: 50px;
    font-family: "Sorts Mill Goudy";
    font-size: 70px;
    line-height: 1.2em;
    font-weight: normal;
    text-align: end;
}
.ocassion .ocassion-content .ocasion-subtitle{
    font-family: "Sorts Mill Goudy";
    font-size: 30px;
    font-style: italic;
    line-height: 1.4em;
    text-align: end;
}
.ocassion .ocassion-container .ocassion-content .btn {
    margin-top: 20px;
    display: flex;
    justify-content: end;
}
.ocassion .ocassion-container .ocassion-content .btn button {
    padding: 10px 35px;
    background-color: white;
    border: 1px solid white;
    color: #051F34;
}
.ocassion .ocassion-container .ocassion-content .btn button:hover{
    background-color: #051F34;
    color: white;
    cursor: pointer;
}
@media (max-width:500px) {
    .ocassion{
        margin-top: 10px;
    }
    .ocassion .ocassion-container {
        display: flex;
        flex-direction: column;
    }
    .ocassion .ocassion-content .ocasion-title {
        text-align: center;
        font-size: 30px;
    }
    .ocassion .ocassion-content .ocasion-subtitle {
        margin-top: 20px;
        text-align: center;
        font-size: 20px;
    }

    .ocassion .ocassion-container .ocassion-content .btn {
        margin-top: 20px;
        display: flex;
        justify-content: center;
    }
    .ocassion .ocassion-img img {
        width: 100%;
        height: 70vh;
    }
}