.products .cards {
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 30px;
}

.products .cards .card .img-container {
    position: relative;
    width: 100%;
    padding-top: 100%;
    border-radius: 10px;
    overflow: hidden;
    background-color: white;
}
.products .cards .card .img-container  img{
    width: 100%;
    height: 100%;
    object-fit: contain;
    top: 0;
    position: absolute;
}
.products .cards .card .title  {
    margin-top: 15px;
    text-align: center;
    font-size: 14px;
    font-weight: 600;
    line-height: 1.3em;
    text-transform: uppercase;
}
.products .cards .card .prix {
    margin-top: 10px;
    font-family: "Montserrat";
    text-align: center;
    font-size: 15px;
    font-weight: 600;
    line-height: 1.3em;
    color: #CB9274;
} 

@media (max-width:500px) {
    .products .cards {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: repeat(4,1fr);
    }
} 
