.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.725);
  backdrop-filter: blur(6px);
  z-index: 999;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav .menu {
  flex: 1;
}
.nav .logo img{
  width: 80px;
}

.nav .menu {
  display: flex;
  flex-direction: row;
  gap: 30px;
  flex: 1;
  list-style: none;
}
.nav ul li:hover{
  cursor: pointer;
  color: #CB9274;
  
}
.nav .logo {
  display: flex;
  justify-content: center;
}
.nav .icon {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  gap: 20px;
}

@media (max-width: 768px) {
  .nav {
    padding: 20px;
  }

  .nav .menu {
    display: flex;
    position: absolute;
    top: -20px;
    right: -100%;
    width: 30vw;
    min-width: 200px;
    height: 100vh;
    background-color: #E3DBD4;
    z-index: 999;
    flex-direction: column;
    padding: 20px;
    border-radius: 10px 0 0 10px;
    transition: 0.3s ease-in-out;
  }

  .nav .menu.active {
    right: 0;
  }

  .nav .icon .menu-icon{
    z-index: 99999;
  }
}
