.colections {
    margin-top: 100px;
}
.colections .title {
  color: #d6b59f;
  font-family: sorts mill goudy, Sans-serif;
  font-size: 260px;
  line-height: 1.1em;
  text-align: center;
}
.colections .colections-img {
    display: flex;
    justify-content: center;
    
}
.colections .colections-img img{
    position: absolute;
    margin-top: -80px;
    height: 40vh;
}
.colections .space {
    height: 40vh;
}
.colections .text {
    position: relative;
    text-align: center;
    font-family: "Sorts Mill Goudy";
    font-size: 35px;
    line-height: 1.5em;
}

@media (max-width:500px) {
    .colections {
        margin-top: 10px;
    }
    .colections .title {
        color: #d6b59f;
        font-family: sorts mill goudy, Sans-serif;
        font-size: 80px;
        line-height: 1.1em;
        text-align: center;
      }
      .colections .colections-img {
        margin-top: 20px;
      }
      .colections .text {
        text-align: center;
        font-size: 25px;
      }
}