.home {
  background-image: url("../../assets/images/home.webp");
  width: 100%;
  height: 90vh;
  background-position: right;
  background-repeat: no-repeat;
  background-size: cover;
}
.home .container {
    display: flex;
    flex-direction: column;
    padding-top: 150px;
    margin-left: 60px;
}
.home .container .sub-title1 {
font-family: Sorts Mill Goudy , Sans-serif;
font-size: 130px;
line-height: 120px;
letter-spacing: -4.33px;
color: #051F34;
}
.home .container .sub-title2{
font-family: Sorts Mill Goudy , Sans-serif;
font-size: 130px;
line-height: 120px;
letter-spacing: -4.33px; 
color: #051F34;
}
.home .container .sub-title3{
    font-size: 36px;
    font-style: italic;
    line-height: 42px;
    letter-spacing: 3px;
 color: #051F34;

}
.home .btn button{
    margin-left: 60px;
    margin-top: 20px;
    padding: 8px 50px;
    border: 1px solid white;
    background-color: white;
    color: #051F34;
    
}
.home .btn button:hover{
    background:#051F34 ;
    color: white;
    border: 1px solid #051F34;
    cursor: pointer;

    
}
.home .media {
    margin-left: 60px ;
    margin-top: 30px;
    display: flex;
    gap: 15px;
}
.home .media svg {
    font-size: 35px;
    color: #172B4D;
}
.home .media svg:hover{
    color: #CB9274;
}

 a {
    text-decoration: none;
}
@media (max-width:500px) {

    /* nav section style */
    .home  {
        display: flex;
        justify-content: center;
        width: 100%;
    }
    .home .container {
        display: flex;
        justify-content: center;
        padding-top: 100px;
        margin-left: 0px;
    }
    .home .container .sub-title1 { 
        display: flex;
        justify-content: center;
        font-size: 60px;
    }
    .home .container .sub-title2 {
        display: flex;
        justify-content: center;
        font-size: 60px;
    }
    .home .container .sub-title3{
        display: flex;
        justify-content: center;
        font-size: 25px
    }
    .home .btn button{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 20px;
        padding: 8px 30px;
        margin: auto;
        margin-top: 50px;
    }
    .home .media {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: 0px ;
        margin-top: 100px;
        display: flex;
        gap: 15px;
    }
    .home .media svg:hover{
        color: #e5a481;
    }
    
     a {
        text-decoration: none;
    }
}
