.footer .content-footer{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 25px;
}
.footer .content-footer .wrap-footer {
    border-top: 3px solid ;
    padding: 30px;
  
}
.footer .content-footer  img{
    width: 80px;
}
.footer .content-footer  .media{
    margin-top: 20px;
    display: flex;
    gap: 15px;
}
.footer .content-footer  .media svg {
    font-size: 30px;
}
.footer .content-footer  .media svg:hover{
    color: #CB9274;
}
.footer .content-footer  .copy{
    color: #CB9274;
    margin-top: 20px;
    font-size: 20px;
}
.footer .content-footer .media svg {
    font-size: 35px;
    color: #172B4D;
}
.footer .content-footer .media svg:hover{
    color: #CB9274;
}

.footer .content-footer .media svg a {
    text-decoration: none;
}

/* responsive */
@media  (max-width: 500px) { 
    .footer .content-footer .copy{
        font-size: 12px;
    }
    .footer .wrap-footer {
        display: flex;
        justify-content: center;
        align-self: center;
    }
}